import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// 修改状态方法 _this.$store.commit('userId',121);
// 引用方法 _this.$store.state.loginInfo.userId

// 登录模块的状态
const loginInfo = {
  state: {
    userInfo: {
      ID: "",
      hidBidsCompanyID: "",
      hidWfEnterpriseID: ""
    }, //用户信息
    showingCId: "", //正在显示的企业ID
    companyInfo: {} //保存企业信息
  },

  mutations: {
    // userInfo 用户信息
    /**
     * 包含
     * userInfo.ID //用户ID
     * userInfo.chbIsDefaultPage //
     * userInfo.hidBidsCompanyID //当前访问的企业ID
     */
    userInfo(state, params) {
      state.userInfo = params;
    },
    userId(state, params) {
      state.userId = params
    },
    showingCId(state, params) {
      state.showingCId = params
    },

    saveCompanyInfoToStore(state, prams) {
      state.companyInfo = prams
    }

  },
  actions: {

  }

}

// 弹窗功能模块
const pop = {
  state: {
    showing: "",
    oldShow: "",

    call: 0,
    config: {
      confirm() { },
      cancel() { },
      text: '非合法操作'
    },//弹窗配置
    params: {}, // 参数
  },
  mutations: {
    // userInfo 用户信息
    /**
     * 包含
     * userInfo.ID //用户ID
     * userInfo.chbIsDefaultPage //
     * userInfo.hidBidsCompanyID //当前访问的企业ID
     */
    /** 
     * 当弹窗之间传参时 可通过
     * 用法 子组件 com-pop 传递state中params
     * 对应的父组件弹窗 监听 params @params 获取弹窗参数
     * 
    */
    showPop(state, params) {
      let popName = "";
      state.oldShow = state.showing
      if (params.constructor.name == "Object") {
        popName = params.popName || '';
        state.params = params.params || {};
        params.config ? state.config = params.config : null;
      }
      if (typeof params == 'string') {
        popName = params;
      }
      state.showing = popName;
      state.call++;
    }

  },
  actions: {
  }

}
export default new Vuex.Store({
  // state: {
  //   // 企业id状态
  //   cId:""

  // },
  // mutations: {

  // },
  // actions: {

  // }

  modules: {
    loginInfo, //登录模块的状态
    pop //弹窗模块
  }

})
