<template>
  <ul id="tabbar">
    <li
      v-for="(item) of tabbarList"
      :key="item.nowPage"
    >
      <router-link :to="item.route">
        <img :src="pos==item.nowPage?item.selectedIconPath:item.iconPath" />
        <!-- <img :src="pos==item.nowPage?item.selectedIconPath:item.iconPath"/> -->
        <p :style='{"color": pos==item.nowPage?"#fff":"#92f6f7"}'>{{item.text}}</p>
      </router-link>
    </li>
  </ul>
</template>

<script>
export default {
  // props: {
  //     pos: Number
  // },
  data() {
    return {
      pos: 0,
      tabbarList: [
        {
          text: "上下链",
          iconPath: require("../../assets/icon/indexF1.png"),
          selectedIconPath: require("../../assets/icon/indexF1-active.png"),
          nowPage: "0",
          route: {
            name: "shangLianTest"
          }
        },
        // {
        //   text: "触客",
        //   iconPath: require("../../assets/icon/indexF2.png"),
        //   selectedIconPath: require("../../assets/icon/indexF2-active.png"),
        //   nowPage: "1",
        //   route: {
        //     name: "chuke"
        //   }
        // },
        {
          text: "订阅",
          iconPath: require("../../assets/icon/indexF2.png"),
          selectedIconPath: require("../../assets/icon/indexF2-active.png"),
          nowPage: "1",
          route: {
            name: "dingyue"
          }
        },
        {
          text: "链圈",
          iconPath: require("../../assets/icon/indexF4.png"),
          selectedIconPath: require("../../assets/icon/indexF4-active.png"),
          nowPage: "2",
          route: {
            name: "lianQuan"
          }
        }
      ]
    };
  },
  computed: {},
  methods: {},
  watch: {
    "$route.name": {
      handler(newVal, oldVal) {
        switch (newVal) {
          case "shangLian":
            this.pos = 0;
            break;
          // case "chuke":
          //   this.pos = 1;
          //   break;
          case "dingyue":
            this.pos = 1;
            break;
          case "lianQuan":
            this.pos = 2;
            break;
          default:
            this.pos = 0;
        }
      }
      // immediate:true
    }
  }
};
</script>
