import Vue from 'vue'
import Router from 'vue-router'
import MySubscript from './views/dingyue/MySubscript'
import LookAround from './views/dingyue/LookAround'


Vue.use(Router)
const router = new Router({
  routes: [   
    {
      path: '/shangLian',
      name: 'shangLian',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "shangLian" */ './views/shangLian/ShangLian.vue'),
      meta: {
        keepAlive: true //需要被缓存
      }
    },
    {
      path: '/shangLianTest',
      name: 'shangLianTest',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "shangLian" */ './views/shangLian/ShangLianTest.vue'),
      meta: {
        keepAlive: false //需要被缓存
      }
    },
    {
      path: '/lianQuan',
      name: 'lianQuan',
      component: () => import(/* webpackChunkName: "lianQuan" */ './views/lianQuan/LianQuan.vue'),
      meta: {
        keepAlive: true //需要被缓存
      }
    },
    {
      path: '/',
      name: 'index',
      component: () => import(/* webpackChunkName: "lianQuan" */ './views/dingyue/dingyue.vue'),
      redirect: '/shangLianTest',
      meta: {
        keepAlive: false //需要被缓存
      }
    },
    // {
    //   path: '/',
    //   name: 'dingyue',
    //   component: () => import(/* webpackChunkName: "lianQuan" */ './views/dingyue/dingyue.vue'),
    //   redirect: '/mySubscript',
    //   children: [
    //     {
    //       path: '/lookAround',
    //       name:'lookAround',
    //       component: () => import('./views/dingyue/LookAround.vue')
    //     },
    //     {
    //       path: '/mySubscript',
    //       name: 'mySubscript',
    //       component: () => import('./views/dingyue/MySubscript.vue')
    //     }
    //   ],
    //   meta: {
    //     keepAlive: true //需要被缓存
    //   }
    // },
    {
      path: '/test',
      name: 'test',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import( './views/Test.vue'),
      meta: {
        keepAlive: true //需要被缓存
      }
    }

  ]
})


export default router;